<script>
  import { onMount } from 'svelte';
  
  let inputElement;
  let password = '';

  const hash = (s) =>
    s.split('').reduce((a, b) => {
      a = (a << 5) - a + b.charCodeAt(0);
      return a & a;
    }, 0);
  
  onMount(() => {
    inputElement.focus();
  });
</script>

{#if hash(password) === 957814433}
  <slot />
{:else}
  <div class="password">
    <a href="https://higsch.com" target="_blank"><img src="https://www.higsch.com/img/small_round_black_logo.svg" alt="higsch logo" /></a>
    <p>Client login</p>
    <label for="password">Password</label>
    <input id="password" bind:value={password} type="password" bind:this={inputElement} />
  </div>
{/if}

